/* General container styles */
.tech-carousel-container {
  padding: 1rem;
  background: linear-gradient(135deg, #0f2027, #203a43, #2c5364);
  color: white;
  border-radius: 10px;
  text-align: center;
  overflow: hidden;
  position: relative;
  width: 100%;
  max-width: 90%;
  margin: 0 auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

/* Heading */
.carousel-heading {
  font-size: 1.8rem;
  font-weight: bold;
  color: #fff;
  letter-spacing: 2px;
  text-transform: uppercase;
}

/* Carousel Wrapper */
.carousel-wrapper {
  position: relative;
  width: 100%;
  height: calc(100% - 2rem);
  overflow: hidden; /* Hide overflow to ensure a clean look */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Carousel Track */
.carousel-track {
  display: flex;
  gap: 2rem;
  animation: bounce-animation 4s ease-in-out infinite; /* Continuous bounce animation */
}

/* Carousel Items */
.carousel-item {
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 10px;
  border-radius: 8px;
  transition: transform 0.3s ease-in-out, background 0.3s ease-in-out;
}

.carousel-item:hover {
  transform: scale(1.1);
}

/* Tech Icons */
.tech-icon {
  width: 80px;
  height: auto;
  margin-bottom: 10px;
  transition: transform 0.3s ease-in-out;
}

.tech-icon:hover {
  transform: scale(1.2);
}

.tech-name {
  font-size: 1rem;
  font-weight: 600;
  color: #fff;
  letter-spacing: 1px;
}

/* Bounce Animation from Left to Right and Right to Left */
@keyframes bounce-animation {
  0% {
    transform: translateX(100%); /* Start from the right edge of the container */
  }
  50% {
    transform: translateX(-100%); /* Move to the left edge of the container */
  }
  100% {
    transform: translateX(100%); /* Return to the right edge of the container */
  }
}
/* Responsive for tablets */
@media (max-width: 768px) {
  .carousel-track {
      gap: 1rem; /* Reduce gap between items */
      animation: bounce-animation 6s ease-in-out infinite; /* Slower bounce for better visibility */
  }
  .tech-icon {
      width: 70px; /* Adjust icon size */
  }
  .tech-name {
      font-size: 0.9rem; /* Adjust text size */
  }
}

/* Responsive for small devices */
@media (max-width: 480px) {
  .carousel-track {
      gap: 0.7rem; /* Reduce gap further */
      animation: bounce-animation 8s ease-in-out infinite; /* Slowest bounce for small screens */
  }
  .tech-icon {
      width: 40px; /* Smaller icons */
  }
  .tech-name {
      font-size: 0.6rem; /* Smaller text */
  }
}
